<template>
    <div class="container">
        <section class="row">
            <div class="col-xs-12">
                <v-breadnav>惠群问答</v-breadnav>
            </div>
        </section>
        <section class="row about-us">
            <div class="col-xs-12 clearfix"> 
                <ul class="nav-list pull-left">
                    <li class="active"><a href="#buy-explain" aria-controls="buy-explain" role="tab" data-toggle="tab">关于我们</a></li>
                    <li><a href="#cost-explain" aria-controls="cost-explain" role="tab" data-toggle="tab">加入我们</a></li>
                    <li><a href="#rent-plan" aria-controls="rent-plan" role="tab" data-toggle="tab">服务条款</a></li>
                    <li><a href="#Transport-rules" aria-controls="Transport-rules" role="tab" data-toggle="tab">关于售后</a></li>
                    <li><a href="#Insurance-services" aria-controls="Insurance-services" role="tab" data-toggle="tab">联系我们</a></li>
                    <li><a href="#other-questions" aria-controls="other-questions" role="tab" data-toggle="tab">购车流程</a></li>    
                </ul>               
                <div class="tab-content">
                     <div role="tabpanel" class="tab-pane active" id="buy-explain">
                         <div class="title"><h3>关于我们</h3></div>
                     </div>
                    <div role="tabpanel" class="tab-pane" id="cost-explain">
                        <div class="title"><h3>加入我们</h3></div>
                    </div>
                    <div role="tabpanel" class="tab-pane" id="rent-plan">
                        <div class="title"><h3>服务条款</h3></div>
                    </div>
                    <div role="tabpanel" class="tab-pane" id="Transport-rules">
                        <div class="title"><h3>关于售后</h3></div>
                    </div>
                    <div role="tabpanel" class="tab-pane" id="Insurance-services">
                        <div class="title"><h3>联系我们</h3></div>
                    </div>
                    <div role="tabpanel" class="tab-pane" id="other-questions">
                        <div class="title"><h3>购车流程</h3></div>
                    </div>
                </div>        
            </div>
        </section>
    </div>
    
</template>

<script>
import vBreadnav from "@/components/pc/breadNav"
import {PC_COMMONURL,SUCCESS_CODE} from '@/assets/js/common'
export default {
    data:function(){
        return{
            questionAnswerList:[]
        }
    },
    components:{
        vBreadnav
    },
    methods:{
        getListData:function(pageNum){
            let _this = this;
            let data = {};
            if(pageNum){
                data.pageNum = pageNum;
            }else{
                data.pageNum = 1;
            }
            data.pageSize = 10;
            data.platForm = "020003";
             $.ajax({
                url:PC_COMMONURL+'/queryQuestionAnswerList',
                type:"get",
                data:data,
                dataType:'json',
                asnyc:true,
                success:function(data){
                    if(data.code == SUCCESS_CODE){
                        console.log(data.data);
                        _this.questionAnswerList = data.data.questionAnswerList;
                    }
                }
            })
        }
    },
    mounted:function(){
        this.getListData();
    }   
}
</script>



